	}

	.pay_type .row {
		margin: 10rpx 0;
		height: 80rpx;
		line-height: 80rpx;
		padding: 0 20rpx;
	}

	.pay_type text {
		vertical-align: middle;
		margin-left: 30rpx;
	}

	.pay_type image {
		width: 50rpx;
		height: 50rpx;
		vertical-align: middle;
	}

	.pay_type .btn {
		text-align: center;
		height: 180rpx;
		line-height: 180rpx;
	}

	.pay_type .btn text {
		display: inline-block;
		width: 500rpx;
		height: 83rpx;
		text-align: center;
		line-height: 83rpx;
		color: #ffffff;
		background: linear-gradient(to right, #fa8b53, #fa4b6d);
	}

	.pay_type .price {
		text-align: center;
		font-size: 40rpx;
		font-weight: 700;
		color: #333333;
		height: 100rpx;
		line-height: 100rpx;
	}
	
	
</style>
